*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0 auto;
  font-family: "PT Root UI", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans";
  font-style: normal;
}

body.popup {
  max-height: 100vh;
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

ul li {
  list-style: none;
}

a {
  border-bottom: none;
}

@keyframes title {
  0% {
    transform: translateY(70px);
    color: #ffffff;
  }
  50% {
    transform: translateY(0);
  }
  99% {
    color: #ffffff;
  }
  100% {
    color: #000000;
  }
}

@keyframes overlay {
  0% {
    height: 100%;
  }
  74% {
    height: 100%;
  }
  100% {
    height: 0;
  }
}

.page-header {
  position: relative;
  padding-bottom: 112px;
}

.page-header__container {
  margin: 0 auto;
  max-width: 1600px;
  padding-left: 96px;
  padding-right: 96px;
}

.page-header__bar {
  display: flex;
  align-items: center;
  padding-top: 55px;
  padding-bottom: 26px;
  margin-bottom: 48px;
}

.page-header__main-nav {
  margin-left: auto;
}

.page-header__title {
  position: relative;
  margin: 0;
  max-width: 710px;
  font-weight: 400;
  font-size: 64px;
  line-height: 72px;
  color: #111112;
  z-index: 10;
}

.page-header__title span {
  display: block;
  overflow: hidden;
}

.page-header__title span:first-child span {
  transition: all 0.2s ease;
  display: block;
  animation: title 1.8s ease-out;
}

.page-header__title span:last-child span {
  transition: all 0.2s ease;
  display: block;
  animation: title 1.6s ease-out 0.3s;
}

.page-header::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: #000000;
  animation: overlay 2s cubic-bezier(0.94, 0.03, 0.72, 0.64);
}

.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.main-nav__item {
  transition: all 0.2s ease;
  position: relative;
  margin-left: 40px;
  padding-bottom: 8px;
  font-size: 16px;
  line-height: 24px;
}

.main-nav__item:hover {
  cursor: pointer;
  color: #009ee5;
}

.main-nav__item:hover .main-nav__subnav-list {
  visibility: visible;
  opacity: 1;
}

.main-nav__item:not(:nth-child(2)) .main-nav__item-name {
  position: relative;
  padding-right: 16px;
}

.main-nav__item:not(:nth-child(2)) .main-nav__item-name::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 7px;
  height: 4px;
  background-image: url("../img/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__link {
  transition: all 0.2s ease;
  color: inherit;
  text-decoration: none;
}

.main-nav__link:hover {
  color: #009ee5;
}

.main-nav__subnav-list {
  transition: all 0.2s ease;
  position: absolute;
  top: 32px;
  width: 256px;
  padding: 32px;
  box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
}

.main-nav__subnav-list--products {
  left: -30px;
}

.main-nav__subnav-list--programs {
  right: -20px;
}

.main-nav__subnav-item:not(:last-child) {
  margin-bottom: 16px;
}

.main-nav__subnav-link {
  transition: all 0.2s ease;
  text-decoration: none;
  color: #000000;
}

.main-nav__subnav-link:hover {
  color: #009ee5;
}

@media (max-width: 1599px) {
  .main-nav__item:hover .main-nav__subnav-list {
    visibility: hidden;
    opacity: 0;
  }
  .main-nav__item-name.js--show + .main-nav__subnav-list {
    visibility: visible;
    opacity: 1;
  }
}

@keyframes banner-overlay {
  0% {
    width: 100%;
  }
  66% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}

.banner__item {
  position: relative;
  width: 25%;
  height: 472px;
  padding: 40px 56px;
  font-size: 32px;
  line-height: 36px;
  color: #ffffff;
  text-decoration: none;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banner__item::before {
  transition: all 0.2s ease;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000000;
  opacity: 5%;
}

.banner__item:hover::before {
  opacity: 30%;
}

.banner__item span {
  position: relative;
  z-index: 10;
}

.banner__item--split {
  background-image: url("../img/banner-split.jpg");
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .banner__item--split {
    background-image: url("../img/banner-split@2x.jpg");
  }
}

.banner__item--split::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-color: #000000;
  animation: banner-overlay 3s cubic-bezier(0.78, 0.01, 0.46, 0.56);
  z-index: 20;
}

.banner__item--mult {
  background-image: url("../img/banner-mult.jpg");
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .banner__item--mult {
    background-image: url("../img/banner-mult@2x.jpg");
  }
}

.banner__item--mult::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-color: #000000;
  animation: banner-overlay 3.2s cubic-bezier(0.78, 0.01, 0.46, 0.56);
  z-index: 20;
}

.banner__item--aircleaner {
  background-image: url("../img/banner-aircleaner.jpg");
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .banner__item--aircleaner {
    background-image: url("../img/banner-aircleaner@2x.jpg");
  }
}

.banner__item--aircleaner::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-color: #000000;
  animation: banner-overlay 3.4s cubic-bezier(0.78, 0.01, 0.46, 0.56);
  z-index: 20;
}

.banner__item--program {
  background-image: url("../img/banner-program.jpg");
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .banner__item--program {
    background-image: url("../img/banner-program@2x.jpg");
  }
}

.banner__item--program::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-color: #000000;
  animation: banner-overlay 3.6s cubic-bezier(0.78, 0.01, 0.46, 0.56);
  z-index: 20;
}
